import reservationApi from '../../api/reservation'

const state = {
  guests: [],
  steps: 3
}

const mutations = {
  setGuests(state, guests) {
    state.guests = guests
  },
  setSteps(state, steps) {
    state.steps = steps
  }
}

const actions = {
  async loadGuests ({ commit }, reservationCode) {
    const guests = await reservationApi.retrieveGuests(reservationCode)
    commit('setGuests', guests)
    commit('setSteps', guests.length)
  }
  // setGuests ({ commit }, guests) {
  //   commit('setGuests', guests)
  // }
}

const getters = {}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}