<template>
  <v-stepper v-model="e1">
    <camera-dialog @saveClose="onSaveClose" @cancelClose="onCancelClose"></camera-dialog>

    <v-stepper-header>
      <template v-for="n in steps">
        <v-stepper-step
          :complete="e1 > n"
          :key="`${n}-step`"
          :step="n"
          editable
        >
          Guest No.{{ n }}
        </v-stepper-step>

        <v-divider
          v-if="n !== steps"
          :key="n"
        ></v-divider>
      </template>
    </v-stepper-header>

    <v-stepper-items>
      <v-stepper-content
        v-for="n in steps"
        :key="`${n}-content`"
        :step="n"
      >
      <v-layout row fill-height>
        <v-flex xs6>
          <span class="headline"><v-btn class="headline" flat @click="showDialog(true)">{{ $t("checkIn.titleId") }}</v-btn></span>
        </v-flex>
        <v-flex xs6>
          <span class="headline"><v-btn class="headline" flat @click="showDialog(false)">{{ $t("checkIn.titleFace") }}</v-btn></span>
        </v-flex>
      </v-layout>  
      <v-layout row fill-height>
        <v-flex xs12>
          <v-container fluid>
            <v-layout row justify-space-between>
              <v-flex xs6>
                <img :src="idPresignedUrl" class="img-responsive" width="500px" @click="showDialog(true)" style="cursor: pointer;"/>
              </v-flex>
              <v-flex xs6>
                <img :src="facePresignedUrl" class="img-responsive" width="500px" @click="showDialog(false)" style="cursor: pointer;"/>
              </v-flex>
            </v-layout>
          </v-container>
        </v-flex>
      </v-layout>

      <v-layout row fill-height>
        <v-flex xs12>
          <v-container fluid>
            <v-layout row justify-space-between>
              <v-flex xs3>
                <v-text-field label="*First name" required hint="*" v-model="guest.firstName" v-if="guest" ></v-text-field>
              </v-flex>
              <v-flex xs3>
<!--                 <v-text-field label="Middle name" :value="guests[e1 - 1].middleName" @input="$set(guests[e1 - 1],'middleName',$event.target.value)" ></v-text-field>  -->
              </v-flex>
              <v-flex xs3>
                <v-text-field label="*Last name" required hint="*" v-model="guest.lastName" v-if="guest" ></v-text-field>
              </v-flex>
            </v-layout>
          </v-container>
          <small>*indicates required field</small>
        </v-flex>
      </v-layout>

      <v-layout row fill-height>
        <v-flex xs12>
          <v-spacer></v-spacer>
          <!-- <v-btn color="blue darken-2" flat @click="showDialog(false)">Take Picture</v-btn> -->
          <v-btn class="headline" flat @click="prevStep(n)">{{ $t("checkIn.previous") }}</v-btn>
          <v-btn class="headline" flat @click="verify()">{{ $t("checkIn.verifyId") }}</v-btn>
          <v-btn class="headline" flat @click="nextStep(n)">{{ $t("checkIn.next") }}</v-btn>
        </v-flex>
      </v-layout>  

      </v-stepper-content>
    </v-stepper-items>
  </v-stepper>
</template>

<script>
import CameraDialog from '@/components/CameraDialog.vue'
import { EventBus } from '@/eventBus'
// import * as queries from '@/graphql/queries'
// import * as mutations from '@/graphql/mutations'
import * as gql from '@/graphql/gql'
import { Storage } from 'aws-amplify'
// import AmplifyStore from '@/store'
import aws_exports from '@/aws-exports'

import { mapState, mapActions } from 'vuex'

export default {
  name: 'CheckIn',
  data () {
    return {
      e1: 1,
      // steps: 1,
      // guests: [],
      imageSaved: 0
    }
  },
  created () {
    // this.guests = await this.getGuests()
    // this.steps = this.guests.length
    this.$store.dispatch('checkIn/loadGuests', this.$store.state.amplify.user.username)
    console.log('steps:', this.steps)
    // this.steps = this.guests.length
  },
  watch: {
    steps (val) {
      console.log('watch steps:', val)
      if (this.e1 > val) {
        this.e1 = val
      }
    }
  },
  computed: {
    // steps: {
    //   get: function () {
    //     return this.guests.length
    //   }
    // },
    guest: {
      get() {
        return this.guests[this.e1 - 1]
      },
      // setter
      set(val) {
        this.guests[this.e1 - 1] = val
      }
    },
    guests: {
      get() {
        return this.$store.state.checkIn.guests;
      },
      set(value) {
        this.$store.commit('checkIn/setGuests', value)
      },
    },
    ...mapState({
      userIdentityId: state => state.amplify.userIdentityId,
      steps: state => state.checkIn.steps
    })
  },
  asyncComputed: {
    idPresignedUrl: {
      get() {
        if (this.guest) {
          if (this.guest.idImgName) {
            return Storage.get(this.guest.idImgName, {level: 'private'})
              .catch((e) => require('../assets/img/id.svg'))
          }
        }

        return require('../assets/img/id.svg')
      },
      watch () {
        this.imageSaved
      }
    },
    facePresignedUrl: {
      get() {        
        if (this.guest) {
          if (this.guest.faceImgName) {
            return Storage.get(this.guest.faceImgName, {level: 'private'})
              .catch((e) => require('../assets/img/face.svg'))
          }
        } 

        return require('../assets/img/face.svg')   
      },
      watch () {
        this.imageSaved
      }
    }
  },
  methods: {
    prevStep (n) {
      if (n === 1) {
        this.e1 = this.steps
      } else {
        this.e1 = n - 1
      }
    },
    nextStep (n) {
      if (n === this.steps) {
        this.e1 = 1
      } else {
        this.e1 = n + 1
      }
    },
    showDialog(isId) {
      EventBus.$emit('dialog', { showDialog: true, isId });
      this.isId = isId;
    },
    async saveImage(val) {

      if (val.base64Img) {
        if (val.isId) {
          let fileName = await Storage.put('id/' + (this.e1 - 1) + '.jpg', new Buffer(val.base64Img, 'base64'), {
            level: 'private',
            contentType: 'image/jpeg'
          })

          let fileKey = 'protected/' + this.userIdentityId + '/' + fileName.key

          this.guest.idImgName = fileName.key
          this.guest.idImgKey = fileKey
          this.guest.idImgBucket = aws_exports.aws_user_files_s3_bucket

        } else {
          let fileName = await Storage.put('face/' + (this.e1 - 1) + '.jpg', new Buffer(val.base64Img, 'base64'), {
            level: 'private',
            contentType: 'image/jpeg'
          })

          let fileKey = 'private/' + this.userIdentityId + '/' + fileName.key

          this.guest.faceImgName = fileName.key
          this.guest.faceImgKey = fileKey
          this.guest.faceImgBucket = aws_exports.aws_user_files_s3_bucket
        }
      }
    },
    async saveGuest() {
      console.log(JSON.stringify(this.guest))
      const res = await this.$Amplify.API.graphql(
        this.$Amplify.graphqlOperation(
          gql.createReservationGuest, 
          { 
            reservationCode: this.guest.reservationCode,
            guestNo: this.guest.guestNo,
            firstName: this.guest.firstName,
            lastName: this.guest.lastName

          })).catch(e => console.log(e))
      console.log('res', res)
    },
    onCancelClose() {
      console.log('cancelClose')
    },
    async onSaveClose(val) {
      console.log('saveClose')
      
      await this.saveImage(val);
      this.imageSaved++

      // await this.saveGuest();
    },
    async getGuests() {
      const resGuests = await this.$Amplify.API.graphql(this.$Amplify.graphqlOperation(gql.getReservationGuest, { reservationCode: this.$store.state.amplify.user.username })).catch(e => console.log(e))

      const resReservation = await this.$Amplify.API.graphql(this.$Amplify.graphqlOperation(gql.getReservation, { reservationCode: this.$store.state.amplify.user.username })).catch(e => console.log(e))


      if (resGuests.data.getReservationGuest.length != 0) {
        return resGuests.data.getReservationGuest;
      }

      // need to show error dialog here
      if (resReservation.data.getReservation.length == 0) {
        // need to show error dialog here
      }
      
      let rtn = [];
      let i
      for (i = 0; i < resReservation.data.getReservation.guestCount; i++) {
        rtn[i] = { reservationCode: this.$store.state.amplify.user, guestNo: i + 1, firstName: '', lastName: ''}
      }
      
      return rtn
    }
  },
  components: {
    cameraDialog: CameraDialog
  }
}
</script>