export const getReservation = `query GetReservation($reservationCode: String!) {
  getReservation(reservationCode: $reservationCode) {
    reservationCode
    guestCount
    checkinDate
  }
}
`;

export const getReservationGuest = `query GetReservationGuest($reservationCode: String!) {
  getReservationGuest(reservationCode: $reservationCode) {
    guestNo
    reservationCode
    passport
    lastName
    firstName
  }
}
`;

export const createReservationGuest = `mutation createReservationGuest($reservationCode: String!, $guestNo: Int!, $lastName: String, $firstName: String) {
  createReservationGuest(input: {reservationCode: $reservationCode, guestNo: $guestNo, lastName: $lastName, firstName: $firstName }) {
    guestNo
    reservationCode
    lastName
    firstName
  }
}
`;


export const updateReservationGuest = `mutation updateReservationGuest($reservationCode: String!, $guestNo: Int!, $lastName: String, $firstName: String) {
  updateReservationGuest(input: {reservationCode: $reservationCode, guestNo: $guestNo, lastName: $lastName, firstName: $firstName }) {
    guestNo
    reservationCode
    lastName
    firstName
  }
}
`;
